import React from "react";
import ListItems from "./ListItems";
import { useSpring, animated } from "react-spring";

// import "./Content.css";

function Pillar(props) {
  // console.log(props);

  const fadeIn = useSpring({
    from: {
      opacity: 0,
      leftMargin: -500,
    },
    to: {
      opacity: 1,
      leftMargin: 0,
    },
    config: { delay: 1000, duration: 1000 },
  });

  return (
    <animated.div
      // style={fadeIn}
      className="Pillar"
    >
      <p className="PillarTop">
        <strong>{props.section}</strong>
      </p>
      <div className="PillarBackground">
        <ul className="PillarBullets"> {props.bullets.map(ListItems)}</ul>
      </div>
    </animated.div>
  );
}

export default Pillar;
