import React from "react";
import { useSpring, animated } from "react-spring";

function Card(props) {
  const fadeIn = useSpring({
    to: { opacity: 1 },
    from: { opacity: 0 },
    config: { delay: 2000 * props.index, duration: 500 },
  });

  console.log(2000 * props.index);

  const fadeInFromLeft = useSpring({
    from: { opacity: 0, marginLeft: -500, marginBottom: -500 },
    to: { opacity: 1, marginLeft: 0, marginBottom: 0 },
    config: { delay: 0, duration: 1000 },
  });

  return (
    <animated.div style={fadeInFromLeft} className="card">
      <div className="top">
        <h2 className="name">{props.name}</h2>
        <img className="circle-img" src={props.img} alt="avatar_img" />
      </div>
      <div className="bottom">
        <p className="info">{props.tel}</p>
        <p className="info">{props.email}</p>
      </div>
    </animated.div>
  );
}

export default Card;
