import React, { Component } from "react";
import { Spring } from "react-spring/renderprops";

import logo from "./logo.svg";

import { Route, Switch, Redirect } from "react-router-dom";

import SkillsPage from "./Content/SkillsPage";
import SolutionsPage from "./Content/SolutionsPage";
import LandingPage from "./Content/LandingPage";
import ContactsPage from "./Content/ContactsPage";
import NavMain from "./Content/NavMain";
import Portfolio from "./Content/Portfolio";

import "./App.css";
import "./App.css";
import "./Content/Content.css";
import Footer from "./Content/Footer";

// Class Version for timer

const routes = ["/", "/skills", "/solutions", "/portfolio", "/contacts"];
const redirectTimeout = 30000;

class App extends Component {
  constructor(props) {
    console.log("Running App.constructor");

    super(props);
    this.state = { routeIndex: 0 };

    console.log("routeIndex now " + this.state.routeIndex);
  }

  // componentDidMount(props) {
  //   console.log("Running App.componentDidMount");
  //   this.handleTimeout = this.handleTimeout.bind(this);

  //   setInterval(this.handleTimeout, redirectTimeout);

  //   console.log("routeIndex now " + this.state.routeIndex);
  // }

  handleTimeout(props) {
    console.log(
      "Running App.handleTimeout " + this.state.routeIndex + " " + routes.length
    );

    if (this.state.routeIndex !== routes.length - 1)
      this.setState((prevState) => ({ routeIndex: prevState.routeIndex + 1 }));
    else
      this.setState({
        routeIndex: 0,
      });

    console.log("routeIndex now " + this.state.routeIndex);
  }

  render(props) {
    console.log("Running App.render " + this.state.routeIndex);

    if (this.state.routeIndex) {
      console.log("Redirecting ... " + routes[this.state.routeIndex]);
    }

    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <NavMain />
        </header>
        <Switch>
          <Route
            exact
            path="/"
            component={LandingPage}
            timeout={redirectTimeout}
          />
          <Route exact path="/skills" component={SkillsPage} />
          <Route exact path="/solutions" component={SolutionsPage} />
          <Route exact path="/portfolio" component={Portfolio} />
          <Route exact path="/contacts" component={ContactsPage} />
          <Route path="*" component={() => "404 NOT FOUND"} />
        </Switch>
        {/* <Redirect to={{ pathname: routes[this.state.routeIndex] }} />

        <Spring
          from={{ number: this.state.routeIndex }}
          to={{ number: redirectTimeout / 1000 }}
          config={{ duration: redirectTimeout }}
        >
          {(props) => (
            <div style={props}>
              <h1 className="Counter">{props.number.toFixed()}</h1>
            </div>
          )}
        </Spring> */}

        {/* <LandingPage />
        <SkillsPage />
        <SolutionsPage />
        <Portfolio /> */}

        <Footer />
      </div>
    );
  }
}

export default App;
