import React from "react";
import Pillar from "./Pillar";
import { useSpring, animated } from "react-spring";

// import "./Content.css";

function PillarSection(props) {
  //   console.log(props);

  const fadeInFromLeft = useSpring({
    from: {
      opacity: 0,
      // marginLeft: 0,
      // marginTop: 50,
    },
    to: {
      opacity: 1,
      // marginTop: -50,
      // marginLeft: 100,
    },
    config: { delay: 0, duration: 500 },
  });

  const fadeUp = useSpring({
    from: {
      opacity: 0,
      marginTop: 0,
    },
    to: {
      opacity: 1,
      marginTop: -250,
    },
    config: { delay: 0, duration: 500 },
  });

  const fadeInFromRight = useSpring({
    from: {
      // opacity: 0,
      marginRight: -500,
      // marginTop: -500,
    },
    to: {
      // opacity: 1,
      marginRight: 0,
      marginTop: 0,
    },
    config: { delay: 0, duration: 400 },
  });

  // return (
  //   <div className="PillarSection">
  //     <div className="PageHeader">
  //       <h1>{props.title}</h1>
  //     </div>
  //     <img className="Images" src={props.image} alt="pillar_img" />
  //     <div className="PillarRow">{props.items.map(Pillar)}</div>
  //   </div>
  // );

  return (
    <div className="PillarSection">
      <animated.div style={fadeInFromRight} className="PageHeader">
        <h1>{props.title}</h1>
      </animated.div>
      <animated.img
        style={fadeInFromLeft}
        className="Images"
        src={props.image}
        alt="pillar_img"
      />
      <animated.div style={fadeUp} className="PillarRow">
        {props.items.map(Pillar)}
      </animated.div>
    </div>
  );
}

export default PillarSection;
